.footer {
  background-color: var(--background-dark-color);
}

.footer-container {
  width: 100%;
}

.footer-span {
  color: var(--text-red-color);
}

.footer-header {
  color: var(--text-light-color);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
  word-wrap: break-word;
  max-width: 300px;
  padding-top: 32px;
  padding-bottom: 32px;
  letter-spacing: -0.02em;
}

.footer-contact {
  display: flex;
  justify-content: space-between;
}

.footer-contact-paragraph {
  color: var(--text-light-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.17;
  word-wrap: break-word;
  padding-bottom: 14px;
}
.footer-address {
  color: var(--text-light-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: -0.02em;
  word-wrap: break-word;
  font-style: normal;
}
.footer-address:hover,
.footer-address:focus {
  color: var(--text-red-color);
}
.footer-socialmedia {
  display: flex;
  gap: 14px;
}

.footer-social-link {
  color: var(--text-light-color);
}
.footer-social-link:hover,
.footer-social-link:focus {
  color: var(--text-red-color);
}

.footer-icons {
  fill: transparent;
  stroke: currentColor;
  stroke-width: 1.5px;
}

.bar {
  width: 100%;
  height: 1px;
  border: 1px rgba(255, 255, 255, 0.1) solid;
  margin-top: 32px;
  margin-bottom: 32px;
}

.footer-navigation {
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding-bottom: 32px;
}
.footer-nav {
  color: var(--text-light-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  letter-spacing: -0.02em;
  word-wrap: break-word;
}
.footer-nav:hover,
.footer-nav:focus {
  color: var(--text-red-color);
}

/* ================================================= TABLET  ========================================= */
@media screen and (min-width: 768px) {
  .footer-contact-box {
    display: flex;
    padding-top: 50px;
    padding-bottom: 50px;
    justify-content: space-between;
  }
  .footer-header {
    padding-top: 0;
    padding-bottom: 0;
    font-size: 28px;
    line-height: 1.2;
    letter-spacing: -0.02em;
  }
  .footer-socialmedia {
    gap: 18px;
  }
  .footer-navigation {
    flex-direction: row;
    gap: 24px;
    padding-bottom: 50px;
  }
  .bar {
    margin-top: 0;
    margin-bottom: 24px;
  }
}

/* ================================================= DESKTOP  ========================================= */
@media screen and (min-width: 1200px) {
  .footer-contact-box {
    padding-bottom: 62px;
  }
}
